import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './guards/auth.guard';
import { noLoginGuard } from './guards/noLogin.guard';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',///mandar a no faoudn porque teien qye teener como requisito un parametro
    pathMatch: 'full'
  },
  {
    path: 'login',
    canActivate: [noLoginGuard],
    loadChildren: () => import('./login/login.module').then( m => m.LoginPageModule)
  },
  {
    path: 'home',
    canActivate: [AuthGuard],
    loadChildren: () => import('./home/home.module').then( m => m.HomePageModule)
  },
  {
    path: 'cert',
    loadChildren: () => import('./pages/certificado/certificado.module').then( m => m.CertificadoPageModule)
  },
  {
    path: 'etapa',
    loadChildren: () => import('./pages/etapa/etapa.module').then( m => m.EtapaPageModule)
  },
  {
    path: 'codelote',
    loadChildren: () => import('./pages/codelote/codelote.module').then( m => m.CodelotePageModule)
  },
  {
    path: 'notfound',
    loadChildren: () => import('./pages/notfound/notfound.module').then( m => m.NotfoundPageModule)
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
