import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { environment as env } from 'src/environments/environment';

@Component({
  selector: 'app-detail-attr',
  templateUrl: './detail-attr.component.html',
  styleUrls: ['./detail-attr.component.scss'],
})
export class DetailAttrComponent implements OnInit {

  constructor(private _modal: ModalController,
    /* public _comp: IonicComponentsService */) { }

  task_detail: any = {
    attributes: []
  };
  endPoint = '';
  isQA = false;
  ngOnInit() {
    this.endPoint = env.url.substr(0, env.url.length - 1);//quitar el "/"

    if (this.task_detail.code == 'QA') this.isQA = true;
    else this.isQA = false;
  }

  closeModal() {
    this._modal.dismiss();
  }

  confirm() {
    this._modal.dismiss({ confirm: true });
  }

}
