import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { IonSlides, ModalController } from '@ionic/angular';
import { ApiMongoService } from 'src/app/services/apiMongo.service';
import { ListCertComponent } from '../list-cert/list-cert.component';
import { environment as env } from 'src/environments/environment';
import { Funciones } from 'src/app/compartido/funciones';
import { IonicComponentsService } from 'src/app/services/ionic-components.service';

@Component({
  selector: 'app-alamcen',
  templateUrl: './alamcen.component.html',
  styleUrls: ['./alamcen.component.scss'],
})
export class AlamcenComponent implements OnInit {


  @ViewChild('slidesDatos', { static: false }) private slidesDatos: IonSlides;

  slideOpts = {
    initialSlide: 0,
    speed: 700,
    autoplay: {
      delay: 2500,
    },
  };

  initEnd = false;
  indexSlide = 0;
  @Input() producersLot: any = [];

  constructor(
    public _comp: IonicComponentsService,
    private _fun: Funciones,
    private _apiMongo: ApiMongoService,
    private _modal: ModalController) { }

  ngOnInit() {
  }

  verSlide() {
    this.slidesDatos.getActiveIndex().then(res => {
      if (!this.initEnd) this.indexSlide = res;
      this.initEnd = false;
    });
  }

  endSlide() {
    this.initEnd = true;
    this.indexSlide = 2;
  }


  async listCred(item) {
    let loading:any  = await this._comp.presentLoading();
    const prod = item;
    let dateNow = Math.floor(new Date().getTime() / 1000);



    let res: any = await this._apiMongo
      .filter(env.COLLECTION.document,
        env.TABLE_SIS.external,
        {
          "data.document.subject.data.dids.address": prod.values[0].id,
          "data.document.stamp_db": { "$lte": dateNow },
          "data.document.iat": { "$lte": dateNow },
          "data.document.exp": { "$gt": dateNow }
        });

 

    if (this._fun.isEmpty(res.result)) {
      this._fun.alertError('No se encontro credenciales para este usuario',true);
      loading.dismiss();
      return;
    }
  
    loading.dismiss();
    /* return; */
    const modal = await this._modal.create({
      component: ListCertComponent,
      componentProps: {
        dataProv: res.result[0].external
      }
    });

    modal.onDidDismiss().then(async (res: any) => {
  
    });
    return await modal.present();

  }

}
